#gavinPlay[data-v-1284b365]{
    /* background:color url x y repeat 图片来自百度图片，按需要更换 */
    background: url(/static/img/guang.d58e1e0.png) center no-repeat;
    /* background-size:auto auto || cover 代表以宽或高填满元素背景 */
    background-size:cover;
    /* 随便设置宽高值，测试 */
    width:200px;
    height:200px;
    /* 设置默认样式，开启3d硬件加速 */
    -webkit-transform:translate3d(0,0,0);
    -moz-transform:translate3d(0,0,0);
    transform:translate3d(0,0,0);
    /* 设置动画，animation:动画名称 动画播放时长单位秒或微秒 动画播放的速度曲线linear为匀速 动画播放次数infinite为循环播放; */
    -webkit-animation:play-data-v-1284b365 3s linear infinite;
    -moz-animation:play-data-v-1284b365 3s linear infinite;
    animation:play-data-v-1284b365 3s linear infinite;
}
@-webkit-keyframes play-data-v-1284b365{
0%  {
        /*
        水平翻转
        
        -webkit-transform:rotateY(0deg);
        
        垂直翻转
        -webkit-transform:rotateX(0deg);
        顺时针旋转 */
